.app-wrapper {
  background-color: rgb(241, 244, 248);
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

.app-wrapper-dark {
  background-color: rgb(71, 71, 71);
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

.app-wrapper-dark {
  color: rgb(244, 244, 244);
}

hr {
  border: 0;
  height: 1px;
  background-image: linear-gradient(to right, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.75), rgba(0, 0, 0, 0));
}

.latest-timestamp {
  text-align: center;
}

.filter-container {
  text-align: center;
  font-size: medium;
  padding: 1%;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
}

.filter {
  justify-content: center;
  text-align: left;
  padding: 1%;
  margin: 0.5%;
  border: 2px solid rgb(228, 230, 236);
  border-radius: 15px;
}

.filter-title {
  text-align: center;
  background-color: rgb(228, 230, 236);
  border-radius: 15px;
  margin: 0.5%;
  padding: 1%;
}

.filter-title-dark {
  text-align: center;
  background-color: rgb(113, 113, 113);
  border-radius: 15px;
  margin: 0.5%;
  padding: 1%;
}

.filter-input {
  align-items: center;
  min-height: fit-content;
  display: flex;
  justify-content: center;
  padding: 2%;
}

.filter-radio {
  align-items: center;
  min-height: fit-content;
  display: flex;
  justify-content: center;
  padding: 2%;

}

.filter-radio > p {
  padding-right: 1%;
}

.spinner {
  text-align: center;
  align-items: center;
  align-self: center;
  align-content: center;
  justify-content: center;
  justify-self: center;
  padding: 5%;
}

.no-data-message {
  text-align: center;
}

.solved-only-disclaimer {
  text-align: center;
  font-size: smaller;
  padding-top: 1%;
}

.toggle-wrapper {
  vertical-align: middle;
}

.custom-toggle-colors.react-toggle--checked .react-toggle-track {
  background-color: #0e249f;
}

.custom-toggle-colors.react-toggle--checked:hover:not(.react-toggle--disabled) .react-toggle-track {
  background-color: #95a3f0;
}